@import url('https://fonts.googleapis.com/css2?family=PT+Sans&display=swap');

@import './variables.scss';
@import './mixins.scss';
* {
    padding : 0;
    margin :  0;
    box-sizing :border-box;
}

.breakpoint {
    margin-top : 1rem;
}


a {
    text-decoration : none;
    color : inherit;
}


#waifus-wrapper {
    display: flex;
    width: 100%;
    height: auto;
    background-color : #f3f3f3;
    color : #303030;

    .switch-wrapper {
        @include respond('tablet') {
            visibility: hidden;
        }

        @include respond('desktop') {
            visibility: hidden;
        }

        position: absolute;
        right: .5rem;
        top : 6rem;
        z-index: 3;

        button {
            padding : .5rem 1rem;
            color : white;
            font-weight : 500;
            font-size : 1.2rem;
            border-radius : 5px;
            background-color :#6294f0;
            cursor: pointer;
        }
    }

    .waifu-filter-active {
        display: block !important;
        height : auto !important;
    }

    .waifu-filter {
        position: relative;
        flex : 0 0 20%;
        padding : 2rem 1rem;
        height: auto;
        background-color : #F06292;
        @include respond('mobile') {
            height : 0px;
            display: none;
        }
        transition : all .5s ease;

        .title-content {
            @include font-scale(2.3rem);
            color : white;
        }

        .form-filter-wrapper {
            padding : 1rem;

            .title-form {
                color : white;
            };

            .form-group {
                display: flex;
                margin : 1rem 0rem;
                flex-direction : column;

                .error-input {
                    padding-top : 1rem;
                    text-align : center;
                    font-weight : 600;
                    font-size : 1.1rem;

                    .error-text {
                        color : white;
                    }
                }

                label {
                    margin : .5rem 0rem;
                    font-weight: 600;
                    letter-spacing : 1px;
                    color : white;
                }

                input[ type="text"],
                input[ type="number"],
                select {
                    border-radius : 7px;
                    padding : .25rem 1rem;
                    border : none;
                    outline : none;
                    background-color : #fefefe;
                }

                select {
                    cursor : pointer;
                }

                input[type="range"] {
                    -webkit-appearance : none;
                    width : 100%;
                }

                input[type=range]::-webkit-slider-runnable-track {
                    height: .35em;
                    background: #35fd57;
                    border: none;
                    border-radius: 3px;
                }
                
                input[type=range]::-webkit-slider-thumb {
                    -webkit-appearance: none;
                    border: none;
                    height: 1.1em;
                    width: 1.1em;
                    border-radius: 50%;
                    background: #ffffff;
                    margin-top: -4px;
                }

                #waifu-max-price::-webkit-slider-runnable-track {
                    background-color : #2fabf3;
                }
                
                input[type=range]:focus {
                    outline: none;
                }

                .submit-filter {
                    outline: none;
                    border : none;
                    cursor : pointer;
                    margin-top : 1rem;
                    padding : .5rem .75rem;
                    font-weight : 500;
                    letter-spacing : 2px;
                    color : white;
                    background-color : #303030;
                    border-radius : 5px;
                    @include font-scale(1.1rem);
                }

                .txt-price  {
                    margin-top : 1rem;
                    text-align : center;
                    font-weight : 600;
                    color : white;
                }
             
            }
        }

    }

    .waifu-all {
        position : relative;
        flex : 1 0 80%;
        @include respond('mobile') {
            width: 100%;
            flex : 1 0 100%;
        }
        width: auto;
        display: flex;
        padding : 2rem 1.25rem;
        flex-direction: column;

        .title-content {
            @include font-scale(1.7rem);
            color : #303030;
            text-align : left;
            @include respond('mobile') {
                text-align : center;
            }
        }

        .input-wrapper {
            position : relative;
            display : flex;
            padding : 1rem 2rem;
            margin : 1rem 0rem;
            padding-top : 0;

            input[ type="text"] {
                width : 80%;
                margin-left : 1rem;
                border-radius : 20px;
                border : 2px solid #8f8f8ffe;
                padding: .25rem 1.25rem;
                outline : none;
            }

            input[ type="text"]:focus {
                border : 2px solid  $base-color-pink;
            }

            button {
                margin : 0rem .5rem;
                padding : .25rem 1.25rem;
                background-color: $base-color-pink;
                border-radius : 20px;
                box-shadow : 1px 4px 4px #3030304d;
                i {
                    color : white;
                }
            }
        }

        .title-wrapper {
            padding : .5rem 2rem;

            .title-content {
                margin-top : 1rem;
            }
        }

        .popular-tags {
            display : flex;
            margin : 1rem 2rem;
            flex-direction: column;


            .popular-tags-wrapper {
                display : flex;
                flex-wrap: wrap;
                justify-content: flex-start;
                margin-top : 1.5rem;
                @include respond('mobile') {
                    justify-content: center;
                }                        
                .popular-tags-content {
                    cursor : pointer;
                    letter-spacing: 2px;
                    padding : .5rem 1rem;
                    background-color : $base-color-pink;
                    border-radius : 5rem;
                    margin : 0 1.5rem;
                    margin-bottom : .5rem;
                    color : white;
                    box-shadow : 1px 2px 7px #30303088;
                    text-transform: uppercase;
                    @include font-scale(.8rem);
                    transform : scale(1);
                    transition:  transform .25s ease;
                    color : white;
                
                }

                .popular-tags-content:hover {
                    transform : scale(1.05);
                }

                .popular-tags-content:nth-child(2) {
                    background-color: #30bbbb;
                }

                .popular-tags-content:nth-child(3) {
                    background-color: #3cc459;
                }

                .popular-tags-content:nth-child(4) {
                    background-color: #c444c4;
                }
                    
            }
        }

                


        .waifu-card-wrapper {
            display: flex;
            flex-wrap: wrap;
            width: auto;
            margin : 0rem .5rem;
            justify-content : space-around;

            .waifu-card {
                display : flex;
                position: relative;
                flex-direction : column;
                min-width: 250px;
                max-width: 300px;
                flex-wrap : wrap;
                padding-top : 1rem;
                background-color : #f8f8f8;
                margin : 1.25rem 1rem;
                text-align: center;
                border-radius : 10px;
                box-shadow : 1px 3px 7px #303030ab;
    
                * {
                     width : 100%;
                    margin : .5rem auto;
                }

                .waifu-name  {
                    @include font-scale(1.7rem)
                }

                .waifu-price {
                    @include font-scale(1.3rem);
                    letter-spacing : 3px;
                }

                .waifu-img {
                    display: block;
                    max-width : 10rem;
                    margin : .5rem auto;
                }

                .ratings {
                    display: flex;
                    width: 60%;
                    flex-direction: row;
                    justify-content: center;
                    margin-bottom : 1rem;

                    i {
                        color : #dee75b;
                        margin : .15rem 0rem;
                        @include font-scale(1.1rem);
                    }
                }

                .tags {
                    display : flex;
                    justify-content: space-around;
                    margin-bottom : 4rem;
                    &-item {
                        display: flex;
                        padding : .5rem 1rem;
                        align-items : center;
                        justify-content: center;
                        background-color : #0bca7b;
                        margin : 0rem .5rem;
                        color : white;
                        border-radius : 5px;
                        cursor : pointer;
                        font-weight: 600;
                        box-sizing: border-box;
                    }

                    &-item:nth-child(2) {
                        background-color : teal;
                    }

                    &-item:nth-child(3) {
                        background-color :  #d8455d;
                    }

                     &-item:nth-child(4) {
                         background-color :  #d8455d;
                    }
                }

                .waifu-btn-detail {
                    position : absolute;
                    bottom : 0;
                    margin-bottom: 0;
                    padding : .75rem;
                    background-color : #303030;
                    color : white;
                    border-radius : 0 0 10px 10px;
                    font-weight: 600;
                    letter-spacing : 2px;
                }

                .waifu-btn-detail:hover {
                    color : #303030;
                    background-color : white;
                }
            }
        }
    }
}